<template>
  <div>
    <Landing />
    <Introduction />
    <DesignProcess />
    <ResearchAndUsabilityStudies />
    <FormerDesign />
    <NewDesign />
    <FinalMockUps />
    <NextProjectRouter
      :currentIdx="1"
    />
  </div>
</template>

<script>
import Landing from './Landing';
import DesignProcess from "./DesignProcess";
import Introduction from "./Introduction";
import ResearchAndUsabilityStudies from "./ResearchAndUsabilityStudies";
import FormerDesign from "./FormerDesign";
import NewDesign from "./NewDesign";
import Solution from "./Solution";
import FinalMockUps from "./FinalMockUps";
import NextProjectRouter from "../NextProjectRouter";

export default {
    name: 'IGN',
    components: {
      Landing,
      Introduction,
      DesignProcess,
      ResearchAndUsabilityStudies,
      FormerDesign,
      NewDesign,
      Solution,
      FinalMockUps,
      NextProjectRouter,
    },
    mounted () {
      this.$ga.page({
        page: '/ign',
        title: 'Ign',
        location: window.location.href
      });
    },

};
</script>



