<template>
  <div>
    <Width100>
      <Paragraph
        subtitle="THE PROBLEM"
        paragraph="IGN Esports’s current website has many issues affecting the overall user experience, such as poor information architecture and inconsistent design across breakpoints. This lack of organization can lead to poor retention and not many users going to IGN Esports as a resource."
        v-bind:center="true"
      />
    </Width100>

    <Width100>
      <Paragraph
        subtitle="Solution"
        paragraph="The solution is to declutter the arrangement and styling of UI elements to be more focused and clean so users can seamlessly navigate from top to bottom to find the content that they need as well as discover topics that might interest them."
        v-bind:center="true"
      />
    </Width100>

    <LineBreak />

  </div>
</template>

<script>
import Carousel from '../common/Carousel';
import Paragraph from '../common/Paragraph';
import LineBreak from '../common/LineBreak';
import VueImage from '../common/VueImage';
import Width50 from '../common/layout/Width50';
import Width100 from '../common/layout/Width100';

import ign_esports_page2 from "../../assets/img/ign_esports_page2.png";
import ign_mobile from "../../assets/img/ign_mobile.jpg";
import ign_schedule_page1 from "../../assets/img/ign_schedule_page1.png";
import ign_schedule_page2 from "../../assets/img/ign_schedule_page2.png";
import ign_navigation_bar1 from "../../assets/img/ign_navigation_bar1.png";
import ign_navigation_bar2 from "../../assets/img/ign_navigation_bar2.png";

export default {
    components: {
      Carousel,
      LineBreak,
      Paragraph,
      VueImage,
      Width50,
      Width100,
    },

    data() {
      return {
        ign_esports_page2: ign_esports_page2,
        ign_mobile: ign_mobile,
        scheduleCarouselItems: [{
          img: ign_schedule_page1,
          alt: "IGN Schedule Page"
        }, {
          img: ign_schedule_page2,
          alt: "IGN Schedule Page"
        }],
        navigationCarouselItems: [{
          img: ign_navigation_bar1,
          alt: "IGN Navigation Bar"
        }, {
          img: ign_navigation_bar2,
          alt: "IGN Navigation Bar"
        }],
        containerStyle: {
          height: "100%"
        },
        imgStyle: {
          padding: "24px",
          width: "80%",
          margin: "0 auto",
        },
      };
    },

};
</script>

<style lang="scss" scoped>
.img-container {
  padding: 0 32px;
  max-width: 720px;
  margin: 32px auto;
}
</style>



