<template>
  <div>
    <Width100>
      <Paragraph
        subtitle="The Solution"
        paragraph="I placed the advertisement banner and the 5 most recent highlight updates on top because they have higher importance being the latest. It encourages users to focus on that part of the screen because it is seen first and makes the content more visually important."
        v-bind:center="true"
      />
    </Width100>

    <Width100>
      <div class="img-container">
        <VueImage :src="ign_desktop_dots" alt="IGN Desktop dots" :isExpandable="true" />
      </div>
    </Width100>

    <Width100>
      <Paragraph
        paragraph="Dots are shown below to allow users to click or swipe through pages sequentially. This technique can lead to a simpler design with less distracting visual clutter."
        v-bind:center="true"
      />
    </Width100>
    <LineBreak />

    <Width100>
      <Paragraph
        paragraph="Right underneath are the highlight updates from past events. I grouped the visual elements near because information related to each other should be close. I placed a gray background for “Major Events 2018” because it is an effective way to attract attention to the next content of the page and also staying consistent with the homepage UI."
        v-bind:center="true"
      />
    </Width100>

    <Width100>
      <div class="img-container">
        <VueImage :src="ign_desktop_highlights" alt="IGN Desktop highlights" :isExpandable="true" />
      </div>
    </Width100>
    <LineBreak />

    <Width100>
      <Paragraph
        paragraph="Below are the calendar schedule and latest esport news tab. I utilized whitespace to bring more focus and attention to the calendar so it is not diluted by grids of images and other content on the page. I made the calendar schedule easier to use by allowing them to filter what games they’re interested in and what week they want to see."
        v-bind:center="true"
      />
    </Width100>

    <Width100>
      <div class="img-container">
        <VueImage :src="ign_desktop_calendar" alt="IGN Desktop calendar" :isExpandable="true" />
      </div>
    </Width100>
    <LineBreak />

    <Width100>
      <Paragraph
        paragraph="Users can quickly jump between tabs to find the latest esport news. If the user is interested in seeing more content, they can explore further information or read further news by clicking on the ‘load more’ button."
        v-bind:center="true"
      />
    </Width100>

    <Width100>
      <div class="img-container">
        <VueImage :src="ign_desktop_news" alt="IGN Desktop news" :isExpandable="true" />
      </div>
    </Width100>
    <LineBreak />
  </div>
</template>

<script>
import Carousel from '../common/Carousel';
import Paragraph from '../common/Paragraph';
import LineBreak from '../common/LineBreak';
import VueImage from '../common/VueImage';
import Width50 from '../common/layout/Width50';
import Width100 from '../common/layout/Width100';

import ign_desktop_dots from "../../assets/img/ign_desktop_dots.jpg";
import ign_desktop_calendar from "../../assets/img/ign_desktop_calendar.jpg";
import ign_desktop_highlights from "../../assets/img/ign_desktop_highlights.jpg";
import ign_desktop_news from "../../assets/img/ign_desktop_news.jpg";

export default {
    components: {
      Carousel,
      LineBreak,
      Paragraph,
      VueImage,
      Width50,
      Width100,
    },

    data() {
      return {
        ign_desktop_dots: ign_desktop_dots,
        ign_desktop_news: ign_desktop_news,
        ign_desktop_calendar: ign_desktop_calendar,
        ign_desktop_highlights: ign_desktop_highlights,
      };
    },

};
</script>

<style lang="scss" scoped>
.img-container {
  padding: 0 32px;
  max-width: 720px;
  margin: 32px auto;
}
</style>



