<template>
  <div>
    <Width100>
      <Paragraph
        subtitle="Final Design"
        paragraph="In my redesign, the text “September Esports Highlight” is the most important information that the user landing on the page needs to know and the first thing the user new to the website should see, so it is placed at the very top along with the 5 most recent highlight updates."
        v-bind:center="true"
        classnames="paragraph"
      />
    </Width100>

    <Width100>
      <Paragraph
        paragraph="Another problem with the current website is that users can only see the current month of Esport events and don’t have the ability to search for past events. In the redesign, I solved this problem by adding a drop down option where users have more selections on what  they want to see and what week to see as well."
        v-bind:center="true"
        classnames="paragraph"
      />
    </Width100>

    <Width100>
      <Paragraph
        paragraph="Lastly, in the redesign I addressed the issue of users finding the ‘latest news’ too overwhelming at the right side of the page. I condensed the info into a different tab next to the schedule so users can easily browse through it more effectively."
        v-bind:center="true"
        classnames="paragraph"
      />
    </Width100>

    <Carousel
      v-bind:items="finalMocksCarouselItems"
      :containerStyle="containerStyle"
      :imgStyle="imgStyle"
    />

    <LineBreak />

    <Width100>
      <Paragraph
        subtitle="Design Challenge"
        paragraph="I knew that the purpose of the IGN’s Esport website is to ​help users stay up to date with the latest esport news and events, but I also had to keep in mind to prioritize advertisement as well. I placed the ads at the very top and on the right side to it would be noticeable but subtle so it wouldn't distract users from their first objective when landing on the site."
        v-bind:center="true"
        classnames="paragraph"
      />
    </Width100>

  </div>
</template>

<script>
import Carousel from '../common/Carousel';
import Paragraph from '../common/Paragraph';
import OrderedList from '../common/OrderedList';
import LineBreak from '../common/LineBreak';
import VueImage from '../common/VueImage';
import Width50 from '../common/layout/Width50';
import Width100 from '../common/layout/Width100';

import ign_desktop_v1 from "../../assets/img/ign_final_design_desktop.png";
import ign_mobile_v1 from "../../assets/img/ign_final_design_mobile.png";

export default {
    components: {
      Carousel,
      LineBreak,
      OrderedList,
      Paragraph,
      VueImage,
      Width50,
      Width100,
    },
    data() {
      return {
        finalMocksCarouselItems: [{
          img: ign_desktop_v1,
          alt: "IGN final mock Desktop version 1",
        }, {
          img: ign_mobile_v1,
          alt: "IGN final mock Mobile version 2",
        },],
        containerStyle: {
          height: "1000px"
        },
        imgStyle: {
          padding: "24px",
          height: "100%",
          margin: "0 auto",
        },
      };
    },
};
</script>

<style lang="scss" scoped>
.paragraph {
  margin-bottom: 16px;
}
</style>



