<template>
  <div>
    <Width100>
      <Paragraph
        title="What is IGN?"
        paragraph="IGN is an american video game and entertainment media platform that brings users the latest news in video games, walkthroughs, and reviews. Currently, IGN’s users span over 22 platforms and 115 countries and provides users with game reviews on a 100 point scale."
        v-bind:center="true"
      />
    </Width100>
  </div>
</template>

<script>
import Carousel from '../common/Carousel';
import Paragraph from '../common/Paragraph';
import LineBreak from '../common/LineBreak';
import VueImage from '../common/VueImage';
import Width50 from '../common/layout/Width50';
import Width100 from '../common/layout/Width100';

export default {
    components: {
      Carousel,
      LineBreak,
      Paragraph,
      VueImage,
      Width50,
      Width100,
    },

    data() {
      return {};
    },

};
</script>

<style lang="scss" scoped>

</style>



