<template>
  <div>
    <Width100>
      <Paragraph
        title="DESIGN PROCESS"
        subtitle="Research & Usability Studies"
        paragraph="My first step was to learn more about who my users were and how they interacted with the current IGN Esports website. I wanted to  understand their mindsets and behaviors so I surveyed 20 people to ask them to navigate IGN’s interface and see what their thoughts were while surfing the website."
        v-bind:center="true"
        classnames="title"
      />
    </Width100>

    <Width100>
      <CenteredText class="link">
        <div class="link_container">
          <AnchorButton
          text="IGN Esports Website Interaction Survey"
          href="https://docs.google.com/forms/d/e/1FAIpQLSfLLMO5vGZBFCxDd378pt-egbl89-fLsKVhrnpCQkOeP_dcAQ/viewform?usp=sf_link"
          />
        </div>
      </CenteredText>
    </Width100>

    <Width100>
      <Paragraph
        paragraph="From there, I sorted my findings into 2 categories: strengths and weaknesses of the current interface."
        v-bind:center="true"
        classnames="strengths"
      />
    </Width100>

    <Width100>
      <div class="img">
        <VueImage :src="strength_weakness" alt="strength & weakness" :isExpandable="true" />
      </div>
    </Width100>

    <LineBreak />

    <Width100>
      <Paragraph
        subtitle="User Stories & Personas"
        paragraph="Before redesigning the esports website, I created 3 personas tied up to a scenario so I could identify more how the end user would interact with the product."
        v-bind:center="true"
        classnames="title"
      />
    </Width100>

    <Width100>
      <div class="img">
        <VueImage :src="ign_persona" alt="strength & weakness" :isExpandable="true" />
      </div>
    </Width100>

    <LineBreak />

  </div>
</template>

<script>
import CenteredText from '../common/CenteredText';
import AnchorButton from '../common/AnchorButton';
import Paragraph from '../common/Paragraph';
import LineBreak from '../common/LineBreak';
import Width100 from '../common/layout/Width100';
import VueImage from '../common/VueImage';

import strength_weakness from "../../assets/img/strength_weakness.png";
import ign_persona from "../../assets/img/ign_persona.png";

export default {
    components: {
      AnchorButton,
      CenteredText,
      LineBreak,
      Paragraph,
      VueImage,
      Width100,
    },
    data() {
      return {
        ign_persona,
        strength_weakness,
      };
    },
};
</script>

<style lang="scss" scoped>
.title {
  margin-bottom: 16px;
}

.strengths {
  margin-top: 16px;
}
.img {
  margin: 32px auto;
  text-align: center;
}

.link_container {
  margin: 16px auto;
}

.link {
  text-align: center;
}

</style>



