<template>
  <Width100>
    <Width50>
      <div class="background left_side">
        <VueImage :src="ign_logo" alt="Banner header logo" :isExpandable="true" />
        <p>
          Reimagined IGN's Esport Page to help users stay up to date with esport news and events.
        </p>
      </div>
    </Width50>
    <Width50>
      <div class="background right_side">
        <VueImage
          classnames="cover_img"
          :src="ign_header"
          alt="Banner header image"
          :isExpandable="true"
        />
      </div>
    </Width50>
  </Width100>
</template>

<script>
import VueImage from '../common/VueImage';
import Width50 from '../common/layout/Width50';
import Width100 from '../common/layout/Width100';

import ign_logo from "../../assets/img/ign_logo.png";
import ign_header from "../../assets/img/ign_header.png";

export default {
    name: 'IgnLanding',
    components: {
      VueImage,
      Width50,
      Width100,
    },
    data() {
      return {
        ign_logo: ign_logo,
        ign_header: ign_header,
      }
    }
};
</script>

<style lang="scss" scoped>
$red: #D51D29;

.container {
  display: flex;
}

.left_side {
  padding: 32px;

  p {
    font-size: 20px;
    margin-top: 16px;
  }
}

.right_side {
}

.background {
  background: $red;
  display: flex;
  flex-direction: column;
  justify-content: center;
  color: white;
  height: 650px;

  .img-container.cover_img {
    height: 100%;

    img {
      height: 100%;
      padding: 32px 0;
    }
  }
}

@media only screen and (max-width: 900px) {
  .background {
    height: 450px;
  }
}
</style>



