<template>
  <div>
    <Width100>
      <Paragraph
        subtitle="Former Design"
        paragraph="After examining who my users were and what their end goal was, I was able to identify the big pain points in the user journey. I realized that most user pains occurred when the user first comes onto  the site because there is a lack of organization that makes browsing through the information difficult."
        v-bind:center="true"
      />
    </Width100>

    <Width100>
        <VueImage :src="ign_former_design" alt="IGN Desktop dots" :isExpandable="true" />
    </Width100>

    <LineBreak />
  </div>
</template>

<script>
import Carousel from '../common/Carousel';
import CenteredText from '../common/CenteredText';
import Paragraph from '../common/Paragraph';
import LineBreak from '../common/LineBreak';
import VueImage from '../common/VueImage';
import Width50 from '../common/layout/Width50';
import Width100 from '../common/layout/Width100';

import ign_former_design from "../../assets/img/ign_former_design.jpeg";
import ign_desktop_dots from "../../assets/img/ign_desktop_dots.jpg";
import ign_desktop_calendar from "../../assets/img/ign_desktop_calendar.jpg";
import ign_desktop_highlights from "../../assets/img/ign_desktop_highlights.jpg";
import ign_desktop_news from "../../assets/img/ign_desktop_news.jpg";

export default {
    components: {
      Carousel,
      CenteredText,
      LineBreak,
      Paragraph,
      VueImage,
      Width50,
      Width100,
    },

    data() {
      return {
        ign_former_design,
        ign_desktop_dots: ign_desktop_dots,
        ign_desktop_news: ign_desktop_news,
        ign_desktop_calendar: ign_desktop_calendar,
        ign_desktop_highlights: ign_desktop_highlights,
      };
    },

};
</script>

<style lang="scss" scoped>
.img-container {
  padding: 0 32px;
  max-width: 720px;
  margin: 32px auto;
}
</style>



